.App {
  height: 100%;
  width: 100vh;
  position: fixed;
  z-index: 0;
  header {
    height: 30%;
    background: $color-1;
    clip-path: polygon(0 0, 100% 0, 100% 100%);
  }
  h1 {
    z-index: 90;
    display: flex;
    padding: 2%;
    justify-content: center;
    width: 100%;
    position: absolute;
    top: 0%;
    color: black;

    background: transparent;
  }
  .media {
    position: absolute;
    top: 6%;
    left: 1%;
    background: transparent;
    li {
      margin-bottom: 15px;
      cursor: pointer;
      transition: ease-in-out 0.3s;

      &:hover {
        transform: scale(1.12);
      }
      img {
        width: 35px;
        height: 35px;
      }
    }
    @media (max-width: 600px) {
      top: 4%;
    }
  }

  background: $color-3;
  width: 100%;
  margin: 0;
  padding: 0;

  .main-container {
    height: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transform: translateY(-80px);
    z-index: 2;
    @media (min-width: 1000px) {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
    }

    li {
      overflow: hidden;
      box-shadow: 0px 0px 6px 3px $color-4;
      border: 2px inset $color-2;
      display: flex;
      margin: 20px 0px 5px 0px;
      background: $color-1;
      border-radius: 60px;
      justify-content: center;
      flex-wrap: wrap;
      width: 300px;
      height: 120px;
      z-index: 2;
      cursor: pointer;
      transition: ease-in-out 0.4s;
      &:hover {
        transform: scale(1.05);
      }
      @media (min-width: 600px) {
        width: 500px;
        height: 160px;
      }
      @media (min-width: 1000px) {
        width: 300px;
        height: 350px;
      }
      @media (min-width: 1300px) {
        width: 400px;
        height: 400px;
      }
      @media (min-width: 1450px) {
        width: 450px;
        height: 450px;
      }
      @media (max-width: 400px) {
        width: 250px;
        height: 100px;
      }

      img {
        height: 70%;
        width: 100%;
        object-fit: cover;
        filter: opacity(90%);
      }
      h2 {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 30%;
        text-align: center;
        margin: 0;
      }
    }
  }
  footer {
    position: fixed;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    width: 100%;
    bottom: 0%;
    z-index: -2;
    background: $color-4;
    height: 30%;
    clip-path: polygon(0 100%, 100% 100%, 100% 0);
  }
  .foot {
    position: absolute;
    display: flex;
    justify-content: flex-end;
    padding: 20px 0px;
    width: 100%;
    z-index: 20;
    bottom: 0;
    .box-1 {
      height: 33.33%;
      display: flex;
      justify-content: flex-end;
      margin-right: 5%;
    }

    .btn {
      line-height: 50px;
      height: 50px;
      text-align: center;
      width: 150px;
      cursor: pointer;
    }
    .btn-one {
      color: #fff;
      transition: all 0.3s;
      position: relative;
    }
    .btn-one span {
      transition: all 0.3s;
    }
    .btn-one::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      opacity: 0;
      transition: all 0.3s;
      border-top-width: 1px;
      border-bottom-width: 1px;
      border-top-style: solid;
      border-bottom-style: solid;
      border-top-color: rgba(255, 255, 255, 0.5);
      border-bottom-color: rgba(255, 255, 255, 0.5);
      transform: scale(0.1, 1);
    }
    .btn-one:hover span {
      letter-spacing: 2px;
    }
    .btn-one:hover::before {
      opacity: 1;
      transform: scale(1, 1);
    }
    .btn-one::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      transition: all 0.3s;

      background-color: rgba(255, 255, 255, 0.1);
    }
    .btn-one:hover::after {
      opacity: 0;
      transform: scale(0.1, 1);
    }
  }
}
